import React, { useEffect } from "react";
import { Element } from "react-scroll";
import "./Experience.css";
import Aos from "aos";
import 'aos/dist/aos.css'
const Experience = () => {
  useEffect(()=>{
    Aos.init();
  },[])
  return (
    <Element id="exp" className="experience-container">
      <h1>Experience</h1>
      <div className="experience-overall">
        <div className="experience-client" data-aos="zoom-out-left" data-aos-duration="2000">
          <h2>2 Yrs</h2>
          <h3>Work Experience</h3>
        </div>
        <div className="experience-project"data-aos="zoom-out-up" data-aos-duration="2000">
          <h2>3+</h2>
          <h3>Projects</h3>
        </div>
        <div className="experience-teaching"data-aos="zoom-out-right" data-aos-duration="2000">
          <h2>2+</h2>
          <h3>Students</h3>
        </div>
      </div>
    </Element>
  );
};

export default Experience;
